import { Link } from "react-router-dom";

const Header = (props) => {
    const { user } = props;

    if (user === null) return <></>;

    return (
        <div className="header-wrapper">
            <ul className="header-menu-list">
                <li className="header-menu-item">
                    <Link className="header-menu-link" to="/" ></Link>
                </li>
                {!user && <li className="header-menu-item">
                    <Link className="header-menu-link" to="/signin" ></Link>
                </li>}
                {!user && <li className="header-menu-item">
                    <Link className="header-menu-link" to="/signup" ></Link>
                </li>}
                {user && <li className="header-menu-item">
                    <Link className="header-menu-link" to="/write" ></Link>
                </li>}
                {user && <li className="header-menu-item">
                    <Link className="header-menu-link" to="/signout"></Link>
                </li>}
            </ul>
        </div>
    )
};

export default Header;